// : frontend/src/pages/dashboard/DashboardPage.tsx

import React, { useState, useEffect } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useAuthStore } from '../../store/authStore';
import { UserRole } from '../../types/auth';
import { 
  ClipboardDocumentListIcon,
  ChatBubbleLeftRightIcon,
  ChartBarIcon,
  UserGroupIcon,
  InformationCircleIcon,
  AcademicCapIcon,
  ClockIcon
} from '@heroicons/react/24/outline';

const DashboardPage = () => {
  const { user } = useAuthStore();
  const navigate = useNavigate();
  const [stats, setStats] = useState({
    completedTests: 0,
    coachingSessions: 0,
    comparisons: 0,
    latestTestDate: null as string | null,
    latestScore: null as number | null
  });

  const getWelcomeMessage = () => {
    const roleMessages = {
      [UserRole.PLAYER]: '',
      [UserRole.COACH]: '',
      [UserRole.FATHER]: '',
      [UserRole.MOTHER]: '',
      [UserRole.ADULT]: ''
    };

    return roleMessages[user?.role as UserRole] || '';
  };

  // 
  useEffect(() => {
    const fetchStats = async () => {
      try {
        // TODO: API
        // 
        const sampleStats = {
          completedTests: 3,
          coachingSessions: 0,
          comparisons: 0,
          latestTestDate: '2024-01-15',
          latestScore: 295
        };
        setStats(sampleStats);
      } catch (error) {
        console.error(':', error);
      }
    };

    fetchStats();
  }, []);

  // 
  const handleViewHistory = () => {
    navigate('/test/history');
  };

  const handleStartNewTest = () => {
    navigate('/test');
  };

  const getAvailableFeatures = () => {
    const baseFeatures = [
      {
        name: '',
        description: '99',
        icon: ClipboardDocumentListIcon,
        link: '/test',
        color: 'bg-blue-500',
        available: true,
        onClick: handleStartNewTest
      },
      {
        name: '',
        description: '',
        icon: ChartBarIcon,
        link: '/test/history',
        color: 'bg-purple-500',
        available: true, // 
        onClick: handleViewHistory
      },
      {
        name: 'AI',
        description: '',
        icon: ChatBubbleLeftRightIcon,
        link: '/coaching',
        color: 'bg-green-500',
        available: false, // 
        onClick: () => navigate('/coaching')
      }
    ];

    // Add 1on1 comparison for eligible users
    if (
      user?.role === UserRole.COACH ||
      user?.head_coach_function ||
      (user?.role === UserRole.FATHER && user?.parent_function) ||
      (user?.role === UserRole.MOTHER && user?.parent_function)
    ) {
      baseFeatures.splice(2, 0, {
        name: '1on1',
        description: '',
        icon: UserGroupIcon,
        link: '/comparison',
        color: 'bg-orange-500',
        available: true, // false  true 
        onClick: () => navigate('/comparison')
      });
    }

    return baseFeatures;
  };

  const getRoleDisplayName = () => {
    const roleNames = {
      [UserRole.PLAYER]: '',
      [UserRole.COACH]: '',
      [UserRole.FATHER]: '',
      [UserRole.MOTHER]: '',
      [UserRole.ADULT]: ''
    };

    let displayName = roleNames[user?.role as UserRole] || user?.role;
    
    if (user?.head_coach_function) {
      displayName += ' ()';
    }
    if (user?.parent_function) {
      displayName += ' ()';
    }

    return displayName;
  };

  const features = getAvailableFeatures();

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      {/* Welcome Section */}
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-gray-900 mb-2">
          {user?.name}
        </h1>
        <p className="text-lg text-gray-600">
          {getWelcomeMessage()}
        </p>
      </div>

      {/* Features Grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6 mb-8">
        {features.map((feature) => (
          feature.available ? (
            <button
              key={feature.name}
              onClick={feature.onClick}
              className="card p-6 hover:shadow-lg transition-shadow duration-200 text-left w-full"
            >
              <div className="flex items-center mb-4">
                <div className={`p-3 rounded-lg ${feature.color}`}>
                  <feature.icon className="h-6 w-6 text-white" />
                </div>
                <h3 className="ml-4 text-lg font-semibold text-gray-900">
                  {feature.name}
                </h3>
              </div>
              <p className="text-gray-600">{feature.description}</p>
            </button>
          ) : (
            <div
              key={feature.name}
              className="card p-6 opacity-50 cursor-not-allowed"
            >
              <div className="flex items-center mb-4">
                <div className={`p-3 rounded-lg bg-gray-400`}>
                  <feature.icon className="h-6 w-6 text-white" />
                </div>
                <h3 className="ml-4 text-lg font-semibold text-gray-900">
                  {feature.name}
                </h3>
              </div>
              <p className="text-gray-600">{feature.description}</p>
              <div className="mt-2">
                <span className="inline-flex items-center px-2 py-1 rounded-md text-xs font-medium bg-yellow-100 text-yellow-800">
                  
                </span>
              </div>
            </div>
          )
        ))}
      </div>

      {/* Quick Stats */}
      <div className="card p-6 mb-8">
        <div className="flex items-center justify-between mb-4">
          <h2 className="text-xl font-semibold text-gray-900"></h2>
          {stats.completedTests > 0 && (
            <button
              onClick={handleViewHistory}
              className="text-sm text-purple-600 hover:text-purple-800 font-medium flex items-center"
            >
              <ClockIcon className="w-4 h-4 mr-1" />
              
            </button>
          )}
        </div>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          <div className="text-center">
            <div className="text-2xl font-bold text-blue-600">{stats.completedTests}</div>
            <div className="text-sm text-gray-500"></div>
            {stats.latestTestDate && (
              <div className="text-xs text-gray-400 mt-1">
                : {new Date(stats.latestTestDate).toLocaleDateString('ja-JP')}
              </div>
            )}
          </div>
          <div className="text-center">
            <div className="text-2xl font-bold text-green-600">{stats.coachingSessions}</div>
            <div className="text-sm text-gray-500"></div>
          </div>
          <div className="text-center">
            <div className="text-2xl font-bold text-orange-600">{stats.comparisons}</div>
            <div className="text-sm text-gray-500"></div>
          </div>
        </div>
        
        {/* Latest Score Display */}
        {stats.latestScore && (
          <div className="mt-4 pt-4 border-t border-gray-200">
            <div className="flex items-center justify-between">
              <span className="text-sm text-gray-600"></span>
              <span className="text-lg font-semibold text-indigo-600">{stats.latestScore}</span>
            </div>
          </div>
        )}
      </div>

      {/* App Information */}
      <div className="card p-6 mb-8">
        <div className="flex items-start">
          <div className="flex-shrink-0">
            <InformationCircleIcon className="h-6 w-6 text-blue-500" />
          </div>
          <div className="ml-4">
            <h2 className="text-xl font-semibold text-gray-900 mb-2">
              
            </h2>
            <p className="text-gray-600 leading-relaxed mb-4">
              AI
              
            </p>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div className="flex items-start">
                <div className="flex-shrink-0">
                  <ClipboardDocumentListIcon className="h-5 w-5 text-blue-500 mt-0.5" />
                </div>
                <div className="ml-2">
                  <h4 className="text-sm font-medium text-gray-900">99</h4>
                  <p className="text-sm text-gray-600">
                    3
                  </p>
                </div>
              </div>
              <div className="flex items-start">
                <div className="flex-shrink-0">
                  <ChatBubbleLeftRightIcon className="h-5 w-5 text-green-500 mt-0.5" />
                </div>
                <div className="ml-2">
                  <h4 className="text-sm font-medium text-gray-900">AI</h4>
                  <p className="text-sm text-gray-600">
                    
                  </p>
                </div>
              </div>
              <div className="flex items-start">
                <div className="flex-shrink-0">
                  <UserGroupIcon className="h-5 w-5 text-orange-500 mt-0.5" />
                </div>
                <div className="ml-2">
                  <h4 className="text-sm font-medium text-gray-900">1on1</h4>
                  <p className="text-sm text-gray-600">
                    
                  </p>
                </div>
              </div>
              <div className="flex items-start">
                <div className="flex-shrink-0">
                  <AcademicCapIcon className="h-5 w-5 text-purple-500 mt-0.5" />
                </div>
                <div className="ml-2">
                  <h4 className="text-sm font-medium text-gray-900"></h4>
                  <p className="text-sm text-gray-600">
                    
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      {/* Getting Started */}
      <div className="card p-6">
        <h2 className="text-xl font-semibold text-gray-900 mb-4">
          {stats.completedTests === 0 ? '' : ''}
        </h2>
        <div className="space-y-3">
          <div className="flex items-center">
            <div className={`flex-shrink-0 w-6 h-6 ${stats.completedTests > 0 ? 'bg-green-500' : 'bg-blue-500'} text-white rounded-full flex items-center justify-center text-sm font-medium`}>
              {stats.completedTests > 0 ? '' : '1'}
            </div>
            <div className="ml-3">
              <p className={stats.completedTests > 0 ? 'text-green-700' : 'text-gray-700'}>
                <strong></strong>
                {stats.completedTests > 0 ? '' : ''}
              </p>
            </div>
          </div>
          <div className="flex items-center">
            <div className={`flex-shrink-0 w-6 h-6 ${stats.completedTests > 0 ? 'bg-blue-500' : 'bg-gray-400'} text-white rounded-full flex items-center justify-center text-sm font-medium`}>
              2
            </div>
            <div className="ml-3">
              <p className={stats.completedTests > 0 ? 'text-gray-700' : 'text-gray-600'}>
                <strong></strong>
                {stats.completedTests > 0 && (
                  <button
                    onClick={handleViewHistory}
                    className="ml-2 text-purple-600 hover:text-purple-800 text-sm underline"
                  >
                    
                  </button>
                )}
              </p>
            </div>
          </div>
          <div className="flex items-center">
            <div className="flex-shrink-0 w-6 h-6 bg-gray-400 text-white rounded-full flex items-center justify-center text-sm font-medium">
              3
            </div>
            <div className="ml-3">
              <p className="text-gray-600">
                <strong>AI</strong>
              </p>
            </div>
          </div>
          <div className="flex items-center">
            <div className="flex-shrink-0 w-6 h-6 bg-gray-400 text-white rounded-full flex items-center justify-center text-sm font-medium">
              4
            </div>
            <div className="ml-3">
              <p className="text-gray-600">
                <strong></strong>
                {stats.completedTests > 0 && (
                  <button
                    onClick={handleStartNewTest}
                    className="ml-2 text-blue-600 hover:text-blue-800 text-sm underline"
                  >
                    
                  </button>
                )}
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default DashboardPage;