import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuthStore } from '../../store/authStore';

const JoinClubPage = () => {
  const [clubId, setClubId] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState(false);
  const [currentUser, setCurrentUser] = useState<any>(null);
  const navigate = useNavigate();
  const { user: authUser } = useAuthStore();

  useEffect(() => {
    loadUserData();
  }, []);

  const loadUserData = async () => {
    // localStorage
    const userStr = localStorage.getItem('user');
    if (userStr) {
      const user = JSON.parse(userStr);
      setCurrentUser(user);
    }

    // API
    return;

    /*
    // API
    try {
      const token = localStorage.getItem('token') || localStorage.getItem('auth-token') || localStorage.getItem('access_token');
      if (!token) {
        console.error('');
        return;
      }
      
      const response = await fetch(
        `${process.env.REACT_APP_API_URL || 'http://localhost:8000'}/api/v1/users/me`,
        {
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json'
          }
        }
      );

      if (response.ok) {
        const data = await response.json();
        setCurrentUser(data);
        // localStorage
        localStorage.setItem('user', JSON.stringify(data));
      } else if (response.status === 401) {
        console.error(': ');
        // 
        // navigate('/login');
      }
    } catch (error) {
      console.error(':', error);
    }
    */
  };

  const handleJoinClub = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!clubId.trim()) {
      setError('ID');
      return;
    }

    setLoading(true);
    setError('');

    try {
      const token = localStorage.getItem('token') || localStorage.getItem('auth-token') || localStorage.getItem('access_token');
      if (!token) {
        setError('');
        setLoading(false);
        return;
      }
      
      console.log(':', token); // 
      console.log('ID:', clubId); // 
      
      // 1. 
      // validate
      
      // 2. 
      const joinResponse = await fetch(
        `${process.env.REACT_APP_API_URL || 'http://localhost:8000'}/api/v1/users/join-club`,
        {
          method: 'PUT',
          headers: {
            'Content-Type': 'application/json',
            'Authorization': `Bearer ${token}`
          },
          body: JSON.stringify({ club_id: clubId.trim() })
        }
      );

      console.log('Join Response Status:', joinResponse.status); // 

      if (joinResponse.ok) {
        const data = await joinResponse.json();
        console.log('Join Success Data:', data); // 
        
        // 
        if (currentUser) {
          const updatedUser = { ...currentUser, club_id: clubId.trim() };
          setCurrentUser(updatedUser);
          localStorage.setItem('user', JSON.stringify(updatedUser));
        }
        
        setSuccess(true);
        setTimeout(() => {
          navigate('/dashboard');
        }, 2000);
      } else {
        // 
        let errorMessage = '';
        try {
          const errorData = await joinResponse.json();
          console.log('Error Data:', errorData); // 
          errorMessage = errorData.detail || 
                        errorData.message || 
                        errorData.error || 
                        errorMessage;
        } catch (e) {
          console.error('Error parsing response:', e);
        }
        setError(typeof errorMessage === 'string' ? errorMessage : JSON.stringify(errorMessage));
      }
    } catch (err) {
      console.error('Network Error:', err);
      setError('');
    } finally {
      setLoading(false);
    }
  };

  const handleLeaveClub = async () => {
    if (!window.confirm('')) {
      return;
    }

    setLoading(true);
    setError('');

    try {
      const token = localStorage.getItem('token') || localStorage.getItem('auth-token') || localStorage.getItem('access_token');
      if (!token) {
        setError('');
        setLoading(false);
        return;
      }
      
      const response = await fetch(
        `${process.env.REACT_APP_API_URL || 'http://localhost:8000'}/api/v1/users/leave-club`,
        {
          method: 'PUT',
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json'
          }
        }
      );

      if (response.ok) {
        // 
        if (currentUser) {
          const updatedUser = { ...currentUser, club_id: null };
          setCurrentUser(updatedUser);
          localStorage.setItem('user', JSON.stringify(updatedUser));
        }
        
        setError('');
        alert('');
        // 
        window.location.reload();
      } else {
        // 
        let errorMessage = '';
        try {
          const errorData = await response.json();
          errorMessage = errorData.detail || 
                        errorData.message || 
                        errorData.error || 
                        errorMessage;
        } catch (e) {
          // JSON
          console.error('Error parsing response:', e);
        }
        setError(typeof errorMessage === 'string' ? errorMessage : JSON.stringify(errorMessage));
      }
    } catch (err) {
      // 
      console.error('Error:', err);
      
      // 
      if (currentUser?.club_id) {
        if (window.confirm('')) {
          const updatedUser = { ...currentUser, club_id: null };
          setCurrentUser(updatedUser);
          localStorage.setItem('user', JSON.stringify(updatedUser));
          alert('');
          window.location.reload();
        }
      }
      setError('');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 flex flex-col justify-center py-12 sm:px-6 lg:px-8">
      <div className="sm:mx-auto sm:w-full sm:max-w-md">
        <h2 className="mt-6 text-center text-3xl font-extrabold text-gray-900">
          
        </h2>
        <p className="mt-2 text-center text-sm text-gray-600">
          {currentUser?.club_id 
            ? `: ${currentUser.club_id}`
            : ''
          }
        </p>
      </div>

      <div className="mt-8 sm:mx-auto sm:w-full sm:max-w-md">
        <div className="bg-white py-8 px-4 shadow sm:rounded-lg sm:px-10">
          {success ? (
            <div className="rounded-md bg-green-50 p-4">
              <div className="flex">
                <div className="ml-3">
                  <h3 className="text-sm font-medium text-green-800">
                    
                  </h3>
                  <div className="mt-2 text-sm text-green-700">
                    <p>...</p>
                  </div>
                </div>
              </div>
            </div>
          ) : currentUser?.club_id ? (
            // 
            <div className="space-y-6">
              <div className="rounded-md bg-blue-50 p-4">
                <div className="flex">
                  <div className="ml-3">
                    <h3 className="text-sm font-medium text-blue-800">
                      
                    </h3>
                    <div className="mt-2 text-sm text-blue-700">
                      <p className="font-semibold">{currentUser.club_id}</p>
                    </div>
                  </div>
                </div>
              </div>

              <div>
                <button
                  onClick={handleLeaveClub}
                  disabled={loading}
                  className="w-full flex justify-center py-2 px-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-red-600 hover:bg-red-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500 disabled:opacity-50"
                >
                  {loading ? '...' : ''}
                </button>
              </div>

              {error && (
                <div className="rounded-md bg-red-50 p-4">
                  <div className="text-sm text-red-800">{error}</div>
                </div>
              )}

              <div className="text-sm text-gray-600 text-center">
                <p> </p>
              </div>
            </div>
          ) : (
            // 
            <form className="space-y-6" onSubmit={handleJoinClub}>
              {error && (
                <div className="rounded-md bg-red-50 p-4">
                  <div className="text-sm text-red-800">{error}</div>
                </div>
              )}

              <div>
                <label htmlFor="clubId" className="block text-sm font-medium text-gray-700">
                  ID
                </label>
                <div className="mt-1">
                  <input
                    id="clubId"
                    name="clubId"
                    type="text"
                    value={clubId}
                    onChange={(e) => setClubId(e.target.value)}
                    required
                    disabled={loading}
                    className="appearance-none block w-full px-3 py-2 border border-gray-300 rounded-md shadow-sm placeholder-gray-400 focus:outline-none focus:ring-primary-500 focus:border-primary-500 sm:text-sm"
                    placeholder=": tokyo-eagles"
                  />
                </div>
                <p className="mt-2 text-sm text-gray-500">
                  ID
                </p>
              </div>

              <div>
                <button
                  type="submit"
                  disabled={loading}
                  className="w-full flex justify-center py-2 px-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-primary-600 hover:bg-primary-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-primary-500 disabled:opacity-50"
                >
                  {loading ? '...' : ''}
                </button>
              </div>
            </form>
          )}

          <div className="mt-6">
            <div className="relative">
              <div className="absolute inset-0 flex items-center">
                <div className="w-full border-t border-gray-300" />
              </div>
              <div className="relative flex justify-center text-sm">
                <span className="px-2 bg-white text-gray-500"></span>
              </div>
            </div>

            <div className="mt-6">
              <button
                onClick={() => navigate('/dashboard')}
                className="w-full flex justify-center py-2 px-4 border border-gray-300 rounded-md shadow-sm text-sm font-medium text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-primary-500"
              >
                
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default JoinClubPage;