// : frontend/src/pages/auth/RegisterPage.tsx

import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import Button from '../../components/common/Button';

const RegisterPage = () => {
  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [formData, setFormData] = useState({
    clubId: '',
    name: '',
    age: '',
    email: '',
    password: '',
    confirmPassword: '',
    role: 'player', // 
    isIndividual: false  // 
  });
  const [agreed, setAgreed] = useState(false);

  // 5
  const roleOptions = [
    { value: 'player', label: '' },
    { value: 'coach', label: '' },
    { value: 'mother', label: '' },
    { value: 'father', label: '' },
    { value: 'adult', label: '' }
  ];

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    // 
    if (!formData.isIndividual && !formData.clubId.trim()) {
      setError('ID');
      return;
    }
    
    if (!formData.name.trim()) {
      setError('');
      return;
    }
    
    if (!formData.age || parseInt(formData.age) < 1) {
      setError('');
      return;
    }
    
    if (!formData.email.trim()) {
      setError('');
      return;
    }
    
    if (!formData.password) {
      setError('');
      return;
    }
    
    if (formData.password !== formData.confirmPassword) {
      setError('');
      return;
    }
    
    if (!agreed) {
      setError('');
      return;
    }

    setLoading(true);
    setError(null);

    try {
      const requestData = {
        is_individual: formData.isIndividual,  // is_individual
        club_id: formData.isIndividual ? undefined : formData.clubId,  // nullundefined
        name: formData.name,
        age: parseInt(formData.age),
        email: formData.email,
        password: formData.password,
        role: formData.role
      };
      
      console.log('Sending registration data:', requestData);
      console.log('Is individual?', formData.isIndividual);
      console.log('Full request data JSON:', JSON.stringify(requestData));

      const response = await fetch(`${process.env.REACT_APP_API_URL}/api/v1/auth/register`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(requestData),
      });

      const data = await response.json();

      if (response.ok) {
        // 
        localStorage.setItem('token', data.access_token);
        localStorage.setItem('user', JSON.stringify(data.user));
        navigate('/dashboard');
      } else {
        // 
        console.error('Registration error details:', data);
        
        // 
        if (Array.isArray(data.detail)) {
          const errorMessages = data.detail.map((err: any) => err.msg || err.message).join(', ');
          setError(errorMessages);
        } else if (typeof data.detail === 'string') {
          setError(data.detail);
        } else {
          setError('');
        }
      }
    } catch (err) {
      setError('');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 flex flex-col justify-center py-12 sm:px-6 lg:px-8">
      <div className="sm:mx-auto sm:w-full sm:max-w-md">
        <h2 className="mt-6 text-center text-3xl font-extrabold text-gray-900">
          
        </h2>
        <p className="mt-2 text-center text-sm text-gray-600">
          
        </p>
      </div>

      <div className="mt-8 sm:mx-auto sm:w-full sm:max-w-md">
        <div className="bg-white py-8 px-4 shadow sm:rounded-lg sm:px-10">
          {/*  */}
          <div className="mb-6 p-4 bg-blue-50 rounded-lg">
            <h3 className="text-lg font-medium text-blue-900 mb-2">
              
            </h3>
            <p className="text-sm text-blue-800 leading-relaxed">
              AI
              
              379AI
            </p>
          </div>

          <form className="space-y-6" onSubmit={handleSubmit}>
            {error && (
              <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded">
                {error}
              </div>
            )}

            {/*  */}
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">
                 <span className="text-red-500">*</span>
              </label>
              <div className="space-y-2">
                <label className="flex items-center">
                  <input
                    type="radio"
                    name="usageType"
                    value="club"
                    checked={!formData.isIndividual}
                    onChange={() => setFormData(prev => ({ ...prev, isIndividual: false }))}
                    className="mr-2 h-4 w-4 text-primary-600 focus:ring-primary-500 border-gray-300"
                  />
                  <span className="text-sm text-gray-700"></span>
                </label>
                <label className="flex items-center">
                  <input
                    type="radio"
                    name="usageType"
                    value="individual"
                    checked={formData.isIndividual}
                    onChange={() => setFormData(prev => ({ ...prev, isIndividual: true, clubId: '' }))}
                    className="mr-2 h-4 w-4 text-primary-600 focus:ring-primary-500 border-gray-300"
                  />
                  <span className="text-sm text-gray-700"></span>
                </label>
              </div>
              <p className="mt-1 text-xs text-gray-500">
                AI
              </p>
            </div>

            {/* ID -  */}
            {!formData.isIndividual && (
              <div>
                <label htmlFor="clubId" className="block text-sm font-medium text-gray-700">
                  ID <span className="text-red-500">*</span>
                </label>
                <div className="mt-1">
                  <input
                    id="clubId"
                    name="clubId"
                    type="text"
                    required={!formData.isIndividual}
                    value={formData.clubId}
                    onChange={handleInputChange}
                    placeholder="ID"
                    className="appearance-none block w-full px-3 py-2 border border-gray-300 rounded-md placeholder-gray-400 focus:outline-none focus:ring-primary-500 focus:border-primary-500"
                  />
                </div>
              </div>
            )}

            {/*  */}
            <div>
              <label htmlFor="name" className="block text-sm font-medium text-gray-700">
                 <span className="text-red-500">*</span>
              </label>
              <div className="mt-1">
                <input
                  id="name"
                  name="name"
                  type="text"
                  required
                  value={formData.name}
                  onChange={handleInputChange}
                  className="appearance-none block w-full px-3 py-2 border border-gray-300 rounded-md placeholder-gray-400 focus:outline-none focus:ring-primary-500 focus:border-primary-500"
                />
              </div>
            </div>

            {/*  */}
            <div>
              <label htmlFor="age" className="block text-sm font-medium text-gray-700">
                 <span className="text-red-500">*</span>
              </label>
              <div className="mt-1">
                <input
                  id="age"
                  name="age"
                  type="number"
                  min="1"
                  max="100"
                  required
                  value={formData.age}
                  onChange={handleInputChange}
                  className="appearance-none block w-full px-3 py-2 border border-gray-300 rounded-md placeholder-gray-400 focus:outline-none focus:ring-primary-500 focus:border-primary-500"
                />
              </div>
            </div>

            {/*  - 5 */}
            <div>
              <label htmlFor="role" className="block text-sm font-medium text-gray-700">
                 <span className="text-red-500">*</span>
              </label>
              <div className="mt-1">
                <select
                  id="role"
                  name="role"
                  required
                  value={formData.role}
                  onChange={handleInputChange}
                  className="appearance-none block w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-primary-500 focus:border-primary-500"
                >
                  {roleOptions.map((option) => (
                    <option key={option.value} value={option.value}>
                      {option.label}
                    </option>
                  ))}
                </select>
              </div>
              <p className="mt-1 text-xs text-gray-500">
                5
              </p>
            </div>

            {/*  */}
            <div>
              <label htmlFor="email" className="block text-sm font-medium text-gray-700">
                 <span className="text-red-500">*</span>
              </label>
              <div className="mt-1">
                <input
                  id="email"
                  name="email"
                  type="email"
                  autoComplete="email"
                  required
                  value={formData.email}
                  onChange={handleInputChange}
                  className="appearance-none block w-full px-3 py-2 border border-gray-300 rounded-md placeholder-gray-400 focus:outline-none focus:ring-primary-500 focus:border-primary-500"
                />
              </div>
            </div>

            {/*  */}
            <div>
              <label htmlFor="password" className="block text-sm font-medium text-gray-700">
                 <span className="text-red-500">*</span>
              </label>
              <div className="mt-1">
                <input
                  id="password"
                  name="password"
                  type="password"
                  autoComplete="new-password"
                  required
                  value={formData.password}
                  onChange={handleInputChange}
                  className="appearance-none block w-full px-3 py-2 border border-gray-300 rounded-md placeholder-gray-400 focus:outline-none focus:ring-primary-500 focus:border-primary-500"
                />
              </div>
            </div>

            {/*  */}
            <div>
              <label htmlFor="confirmPassword" className="block text-sm font-medium text-gray-700">
                 <span className="text-red-500">*</span>
              </label>
              <div className="mt-1">
                <input
                  id="confirmPassword"
                  name="confirmPassword"
                  type="password"
                  autoComplete="new-password"
                  required
                  value={formData.confirmPassword}
                  onChange={handleInputChange}
                  className="appearance-none block w-full px-3 py-2 border border-gray-300 rounded-md placeholder-gray-400 focus:outline-none focus:ring-primary-500 focus:border-primary-500"
                />
              </div>
            </div>

            {/*  */}
            <div className="flex items-start">
              <div className="flex items-center h-5">
                <input
                  id="agree"
                  name="agree"
                  type="checkbox"
                  checked={agreed}
                  onChange={(e) => setAgreed(e.target.checked)}
                  className="focus:ring-primary-500 h-4 w-4 text-primary-600 border-gray-300 rounded"
                />
              </div>
              <div className="ml-3 text-sm">
                <label htmlFor="agree" className="font-medium text-gray-700">
                   <span className="text-red-500">*</span>
                </label>
                <p className="text-gray-500 text-xs mt-1">
                  
                  
                </p>
              </div>
            </div>

            <div>
              <Button
                type="submit"
                disabled={loading}
                className="w-full"
              >
                {loading ? '...' : ''}
              </Button>
            </div>
          </form>

          <div className="mt-6">
            <div className="text-center">
              <span className="text-sm text-gray-600">
                
              </span>
              <Link
                to="/login"
                className="ml-1 font-medium text-primary-600 hover:text-primary-500"
              >
                
              </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default RegisterPage;