// : frontend/src/pages/admin/UserAdminPage.tsx

import React, { useState, useEffect } from 'react';
import AdminHeader from '../../components/admin/AdminHeader';

interface User {
  user_id: string;
  name: string;
  age: number;
  email: string;
  role: string;
  club_id: string;
  parent_function: boolean;
  head_coach_function: boolean;
  created_date: string;
  updated_date?: string;
}

interface UserStatistics {
  total_users: number;
  role_statistics: Record<string, number>;
  club_statistics: Array<{ club_id: string; user_count: number; }>;
}

// 
const ROLES = {
  'player': '',
  'coach': '',
  'mother': '',
  'father': '',
  'adult': ''
};

const UserAdminPage = () => {
  const [users, setUsers] = useState<User[]>([]);
  const [statistics, setStatistics] = useState<UserStatistics | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [searchTerm, setSearchTerm] = useState('');
  const [filterRole, setFilterRole] = useState('');
  const [filterClub, setFilterClub] = useState('');
  const [editingUser, setEditingUser] = useState<User | null>(null);

  // 
  const fetchUsers = async () => {
    try {
      setLoading(true);
      let url = `${process.env.REACT_APP_API_URL || 'http://localhost:8000'}/api/v1/admin/users`;
      
      const params = new URLSearchParams();
      if (searchTerm) params.append('search', searchTerm);
      if (filterRole) params.append('role', filterRole);
      if (filterClub) params.append('club_id', filterClub);
      
      if (params.toString()) {
        url += `?${params.toString()}`;
      }
      
      const response = await fetch(url);
      const data = await response.json();
      
      if (response.ok) {
        setUsers(data.users || data);
        setError(null);
      } else {
        setError('');
      }
    } catch (err) {
      setError('');
    } finally {
      setLoading(false);
    }
  };

  // 
  const fetchStatistics = async () => {
    try {
      const response = await fetch(`${process.env.REACT_APP_API_URL || 'http://localhost:8000'}/api/v1/admin/users/statistics/summary`);
      const data = await response.json();
      
      if (response.ok) {
        setStatistics(data);
      }
    } catch (err) {
      console.error(':', err);
    }
  };

  useEffect(() => {
    fetchUsers();
    fetchStatistics();
  }, [searchTerm, filterRole, filterClub]);

  // 
  const handleUpdateRole = async (userId: string, newRole: string) => {
    try {
      const token = localStorage.getItem('admin_token');
      const response = await fetch(`${process.env.REACT_APP_API_URL || 'http://localhost:8000'}/api/v1/admin/users/${userId}/role`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': token ? `Bearer ${token}` : '',
        },
        body: JSON.stringify({ new_role: newRole }),
      });

      if (response.ok) {
        setEditingUser(null);
        fetchUsers();
        fetchStatistics();
        alert('');
      } else {
        const errorData = await response.json();
        alert(`: ${errorData.detail || ''}`);
      }
    } catch (err) {
      alert('');
    }
  };

  // /
  const handleToggleActive = async (userId: string, isActive: boolean) => {
    try {
      const token = localStorage.getItem('admin_token');
      const endpoint = isActive ? 'activate' : 'deactivate';
      const response = await fetch(`${process.env.REACT_APP_API_URL || 'http://localhost:8000'}/api/v1/admin/users/${userId}/${endpoint}`, {
        method: 'PUT',
        headers: {
          'Authorization': token ? `Bearer ${token}` : '',
        },
      });

      if (response.ok) {
        fetchUsers();
        fetchStatistics();
        alert(`${isActive ? '' : ''}`);
      } else {
        const errorData = await response.json();
        alert(`: ${errorData.detail || ''}`);
      }
    } catch (err) {
      alert('');
    }
  };

  // 
  const handleDeleteUser = async (userId: string, userName: string) => {
    if (!window.confirm(`${userName}\n`)) {
      return;
    }

    try {
      const token = localStorage.getItem('admin_token');
      const response = await fetch(`${process.env.REACT_APP_API_URL || 'http://localhost:8000'}/api/v1/admin/users/${userId}`, {
        method: 'DELETE',
        headers: {
          'Authorization': token ? `Bearer ${token}` : '',
        },
      });

      if (response.ok) {
        fetchUsers();
        fetchStatistics();
        alert('');
      } else {
        const errorData = await response.json();
        alert(`: ${errorData.detail || ''}`);
      }
    } catch (err) {
      alert('');
    }
  };

  // 
  const handleToggleHeadCoach = async (userId: string, userName: string, currentStatus: boolean) => {
    const newStatus = !currentStatus;
    const statusText = newStatus ? '' : '';
    
    if (!window.confirm(`${userName}${statusText}`)) {
      return;
    }

    try {
      const token = localStorage.getItem('admin_token');
      const response = await fetch(`${process.env.REACT_APP_API_URL || 'http://localhost:8000'}/api/v1/admin/users/${userId}/head-coach`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': token ? `Bearer ${token}` : '',
        },
        body: JSON.stringify({ is_head_coach: newStatus }),
      });

      if (response.ok) {
        fetchUsers();
        alert(`${statusText}`);
      } else {
        const errorData = await response.json();
        alert(`: ${errorData.detail || ''}`);
      }
    } catch (err) {
      alert('');
    }
  };

  if (loading) {
    return (
      <>
        <AdminHeader />
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
          <div className="text-center">...</div>
        </div>
      </>
    );
  }

  return (
    <>
      <AdminHeader />
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-gray-900 mb-4"></h1>
          
          {/*  */}
          {statistics && (
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
              <div className="bg-white rounded-lg shadow p-4">
                <div className="text-sm font-medium text-gray-500"></div>
                <div className="text-2xl font-bold text-gray-900">{statistics.total_users}</div>
              </div>
              <div className="bg-white rounded-lg shadow p-4">
                <div className="text-sm font-medium text-gray-500"></div>
                <div className="text-sm text-gray-600">
                  {Object.entries(statistics.role_statistics).map(([role, count]) => (
                    <div key={role}>{ROLES[role as keyof typeof ROLES]}: {count}</div>
                  ))}
                </div>
              </div>
              <div className="bg-white rounded-lg shadow p-4">
                <div className="text-sm font-medium text-gray-500"></div>
                <div className="text-2xl font-bold text-blue-600">{statistics.club_statistics?.length || 0}</div>
              </div>
            </div>
          )}

          {/*  */}
          <div className="bg-white rounded-lg shadow p-4 mb-6">
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  
                </label>
                <input
                  type="text"
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  placeholder=""
                  className="w-full p-2 border border-gray-300 rounded focus:ring-2 focus:ring-primary-500"
                />
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  
                </label>
                <select
                  value={filterRole}
                  onChange={(e) => setFilterRole(e.target.value)}
                  className="w-full p-2 border border-gray-300 rounded focus:ring-2 focus:ring-primary-500"
                >
                  <option value=""></option>
                  {Object.entries(ROLES).map(([value, label]) => (
                    <option key={value} value={value}>{label}</option>
                  ))}
                </select>
              </div>
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  
                </label>
                <select
                  value={filterClub}
                  onChange={(e) => setFilterClub(e.target.value)}
                  className="w-full p-2 border border-gray-300 rounded focus:ring-2 focus:ring-primary-500"
                >
                  <option value=""></option>
                  {statistics?.club_statistics?.map((club) => (
                    <option key={club.club_id} value={club.club_id}>
                      {club.club_id} ({club.user_count})
                    </option>
                  ))}
                </select>
              </div>
            </div>
          </div>
        </div>

        {error && (
          <div className="mb-4 p-4 bg-red-100 border border-red-400 text-red-700 rounded">
            {error}
          </div>
        )}

        {/*  */}
        <div className="bg-white shadow overflow-hidden sm:rounded-md">
          <ul className="divide-y divide-gray-200">
            {users.map((user) => (
              <li key={user.user_id} className="px-6 py-4">
                {editingUser?.user_id === user.user_id ? (
                  // 
                  <div className="space-y-4">
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1">
                          
                        </label>
                        <select
                          value={editingUser.role}
                          onChange={(e) => setEditingUser({...editingUser, role: e.target.value})}
                          className="w-full p-2 border border-gray-300 rounded focus:ring-2 focus:ring-primary-500"
                        >
                          {Object.entries(ROLES).map(([value, label]) => (
                            <option key={value} value={value}>{label}</option>
                          ))}
                        </select>
                      </div>
                    </div>
                    <div className="flex space-x-2">
                      <button 
                        onClick={() => handleUpdateRole(editingUser.user_id, editingUser.role)}
                        className="bg-blue-500 hover:bg-blue-600 text-white px-4 py-2 rounded"
                      >
                        
                      </button>
                      <button 
                        onClick={() => setEditingUser(null)}
                        className="bg-gray-500 hover:bg-gray-600 text-white px-4 py-2 rounded"
                      >
                        
                      </button>
                    </div>
                  </div>
                ) : (
                  // 
                  <div className="flex items-center justify-between">
                    <div className="flex-1">
                      <div className="flex items-center space-x-4">
                        <div className="flex-shrink-0">
                          <div className="w-10 h-10 bg-gray-300 rounded-full flex items-center justify-center">
                            <span className="text-sm font-medium text-gray-700">
                              {user.name.charAt(0)}
                            </span>
                          </div>
                        </div>
                        <div className="flex-1 min-w-0">
                          <div className="flex items-center space-x-2">
                            <p className="text-sm font-medium text-gray-900 truncate">
                              {user.name}
                            </p>
                            <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-blue-100 text-blue-800">
                              {ROLES[user.role as keyof typeof ROLES]}
                            </span>
                            <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-gray-100 text-gray-800">
                              {user.club_id}
                            </span>
                            {user.head_coach_function && (
                              <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-purple-100 text-purple-800">
                                
                              </span>
                            )}
                            {user.parent_function && (
                              <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-green-100 text-green-800">
                                
                              </span>
                            )}
                          </div>
                          <p className="text-sm text-gray-500">{user.email}</p>
                          <p className="text-xs text-gray-400">
                            : {user.age} | : {new Date(user.created_date).toLocaleDateString('ja-JP')}
                          </p>
                        </div>
                      </div>
                    </div>
                    <div className="ml-4 flex space-x-2">
                      <button
                        onClick={() => setEditingUser(user)}
                        className="bg-gray-500 hover:bg-gray-600 text-white px-3 py-1 rounded text-sm"
                      >
                        
                      </button>
                      <button
                        onClick={() => handleToggleHeadCoach(user.user_id, user.name, user.head_coach_function)}
                        className={`${
                          user.head_coach_function 
                            ? 'bg-purple-600 hover:bg-purple-700' 
                            : 'bg-purple-400 hover:bg-purple-500'
                        } text-white px-3 py-1 rounded text-sm`}
                        title={user.head_coach_function ? '' : ''}
                      >
                        HC
                      </button>
                      <button
                        onClick={() => handleToggleActive(user.user_id, true)}
                        className="bg-yellow-500 hover:bg-yellow-600 text-white px-3 py-1 rounded text-sm"
                        title="/is_active"
                      >
                        
                      </button>
                      <button
                        onClick={() => handleDeleteUser(user.user_id, user.name)}
                        className="bg-red-600 hover:bg-red-700 text-white px-3 py-1 rounded text-sm"
                      >
                        
                      </button>
                    </div>
                  </div>
                )}
              </li>
            ))}
          </ul>
        </div>

        {users.length === 0 && (
          <div className="text-center py-12">
            <p className="text-gray-500"></p>
          </div>
        )}
      </div>
    </>
  );
};

export default UserAdminPage;