// src/components/history/ExportOptions.tsx

import React, { useState } from 'react';
import {
  DocumentTextIcon,
  DocumentIcon,
  PhotoIcon,
  ArrowDownTrayIcon,
  ArrowPathIcon
} from '@heroicons/react/24/outline';
import { TestResultWithAnalysis } from '../../types/test';
import { testService } from '../../services/testService';
import Button from '../common/Button';

interface ExportOptionsProps {
  results: TestResultWithAnalysis[];
  selectedResultIds?: Set<string>;
  chartRef?: React.RefObject<HTMLDivElement>;
}

const ExportOptions: React.FC<ExportOptionsProps> = ({
  results,
  selectedResultIds,
  chartRef
}) => {
  const [isExporting, setIsExporting] = useState(false);
  const [exportType, setExportType] = useState<'csv' | 'pdf' | 'image' | null>(null);

  const handleExportCSV = async () => {
    setIsExporting(true);
    setExportType('csv');
    
    try {
      // 
      const resultsToExport = selectedResultIds && selectedResultIds.size > 0
        ? results.filter(r => selectedResultIds.has(r.result_id))
        : results;

      testService.exportToCSV(resultsToExport);
      
      // 
      console.log('CSV');
    } catch (error) {
      console.error('CSV:', error);
      alert('CSV');
    } finally {
      setIsExporting(false);
      setExportType(null);
    }
  };

  const handleExportPDF = async () => {
    setIsExporting(true);
    setExportType('pdf');
    
    try {
      // PDF
      // : jsPDFpdfmake
      alert('PDF');
      
      // API:
      // const blob = await testService.exportHistory('pdf', {
      //   resultIds: Array.from(selectedResultIds || [])
      // });
      // const url = URL.createObjectURL(blob);
      // const a = document.createElement('a');
      // a.href = url;
      // a.download = `test_history_${new Date().toISOString().split('T')[0]}.pdf`;
      // a.click();
      // URL.revokeObjectURL(url);
    } catch (error) {
      console.error('PDF:', error);
      alert('PDF');
    } finally {
      setIsExporting(false);
      setExportType(null);
    }
  };

  const handleExportImage = async () => {
    setIsExporting(true);
    setExportType('image');
    
    try {
      if (!chartRef?.current) {
        alert('');
        return;
      }

      // Chart.jscanvas
      const canvas = chartRef.current.querySelector('canvas');
      if (!canvas) {
        alert('');
        return;
      }

      // canvas
      const url = canvas.toDataURL('image/png');
      const a = document.createElement('a');
      a.href = url;
      a.download = `test_history_chart_${new Date().toISOString().split('T')[0]}.png`;
      a.click();
      
      console.log('');
    } catch (error) {
      console.error(':', error);
      alert('');
    } finally {
      setIsExporting(false);
      setExportType(null);
    }
  };

  // 
  const generateReport = (resultsToExport: TestResultWithAnalysis[]) => {
    if (resultsToExport.length === 0) return '';

    const latestResult = resultsToExport[0];
    const oldestResult = resultsToExport[resultsToExport.length - 1];
    
    // 
    const latestTotal = latestResult.self_esteem_total + latestResult.courage + 
                       latestResult.resilience + latestResult.cooperation + 
                       latestResult.natural_acceptance + latestResult.non_rationality;
    const oldestTotal = oldestResult.self_esteem_total + oldestResult.courage + 
                       oldestResult.resilience + oldestResult.cooperation + 
                       oldestResult.natural_acceptance + oldestResult.non_rationality;
    const improvementRate = ((latestTotal - oldestTotal) / oldestTotal * 100).toFixed(1);

    return `

: ${new Date().toLocaleDateString('ja-JP')}

: ${new Date(oldestResult.test_date).toLocaleDateString('ja-JP')} - ${new Date(latestResult.test_date).toLocaleDateString('ja-JP')}
: ${resultsToExport.length}

:
- : ${latestResult.athlete_type}
- : ${latestTotal}
- : ${latestResult.self_esteem_total}

:
- : ${improvementRate}%
- : ${getMaxImprovedCategory(oldestResult, latestResult)}
`;
  };

  const getMaxImprovedCategory = (oldest: TestResultWithAnalysis, latest: TestResultWithAnalysis): string => {
    const categories = [
      { name: '', old: oldest.self_esteem_total, new: latest.self_esteem_total },
      { name: '', old: oldest.courage, new: latest.courage },
      { name: '', old: oldest.resilience, new: latest.resilience },
      { name: '', old: oldest.cooperation, new: latest.cooperation },
      { name: '', old: oldest.natural_acceptance, new: latest.natural_acceptance },
      { name: '', old: oldest.non_rationality, new: latest.non_rationality }
    ];

    let maxImprovement = 0;
    let maxCategory = '';

    categories.forEach(cat => {
      const improvement = ((cat.new - cat.old) / cat.old) * 100;
      if (improvement > maxImprovement) {
        maxImprovement = improvement;
        maxCategory = cat.name;
      }
    });

    return `${maxCategory} (+${maxImprovement.toFixed(1)}%)`;
  };

  return (
    <div className="flex flex-wrap gap-2">
      <Button
        variant="secondary"
        size="sm"
        onClick={handleExportCSV}
        disabled={isExporting || results.length === 0}
        className="inline-flex items-center"
      >
        {isExporting && exportType === 'csv' ? (
          <>
            <ArrowPathIcon className="w-4 h-4 mr-2 animate-spin" />
            ...
          </>
        ) : (
          <>
            <DocumentTextIcon className="w-4 h-4 mr-2" />
            CSV
          </>
        )}
      </Button>

      <Button
        variant="secondary"
        size="sm"
        onClick={handleExportPDF}
        disabled={isExporting || results.length === 0}
        className="inline-flex items-center"
      >
        {isExporting && exportType === 'pdf' ? (
          <>
            <ArrowPathIcon className="w-4 h-4 mr-2 animate-spin" />
            ...
          </>
        ) : (
          <>
            <DocumentIcon className="w-4 h-4 mr-2" />
            PDF
          </>
        )}
      </Button>

      {chartRef && (
        <Button
          variant="secondary"
          size="sm"
          onClick={handleExportImage}
          disabled={isExporting}
          className="inline-flex items-center"
        >
          {isExporting && exportType === 'image' ? (
            <>
              <ArrowPathIcon className="w-4 h-4 mr-2 animate-spin" />
              ...
            </>
          ) : (
            <>
              <PhotoIcon className="w-4 h-4 mr-2" />
              
            </>
          )}
        </Button>
      )}

      {selectedResultIds && selectedResultIds.size > 0 && (
        <span className="text-sm text-gray-600 self-center ml-2">
          {selectedResultIds.size}
        </span>
      )}
    </div>
  );
};

export default ExportOptions;